.projects {
    justify-content: center;
    top: -250px;
}
.projects .column {
    float: left;
    width: 45%;
    padding: 0 10px 10px;
}
    
.projects .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
  /* Clear floats after the columns */
.projects .row:after {
    content: "";
    display: table;
    clear: both;
}
  
/* Responsive columns */
@media screen and (max-width: 800px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}